.mfa-registration-container {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: max-content auto min-content;
}

.mfa-registration-container__description {
  grid-column: 1;
  grid-row: 1;
}

.mfa-registration-container__status {
  align-items: center;
  display: flex;
  grid-column: 1;
  grid-row: 2;
}

.status-message__icon {
  color: #0071c4;
}

.status-message__description {
  margin-left: 1em;
}

.mfa-registration-container__thumbnail {
  grid-column: 2;
  grid-row: 1/span 2;
  height: 260px;
  padding: 20px;
  width: 260px;
}

.mfa-registration-container__actions {
  grid-column: 1;
  grid-row: 3;
}




